.attribute-container{
    display: flex;
    border-style: solid;
    border-color: black;
    border-radius: 2rem;
    border-width: 0.2rem;
    margin: 3rem;
    background-color: cornsilk;
    justify-content: start;
    flex-direction: row;
    width: min-content;
}


.attribute-arrow{
    border: solid black;
    border-width: 0 3px 3px 0;
    display: inline-block;
    padding: 5px;
    margin: 4px;
    transform: rotate(-45deg);
    -webkit-transform: rotate(-45deg);
}

.attribute-arrow:hover {
    border-bottom-width: 4px;
    border-right-width: 4px;
  }


.cirlce{
    height: 25px;
    width: 25px;
    background-color: cornsilk;
    border: solid black;
    border-radius: 50%;
    margin: 1rem;
}


.list-container{
    display: inline-flex;
}

@media( max-width:768px){
    .list-container{
        display: block;
            
    }
}




.attribute-box{
display: flex;
border-style: solid;
border-color: black;
border-radius: 2rem;
border-width: 0.2rem;
margin-bottom: 0.2rem;
margin-right: 2rem;
justify-content: center;
padding: 0.1rem;
width: min-content;
}